.list{
    color: white;
    width: 300px;
    background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);
    font-size: 20px;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p{
    padding: 10px;
    position: relative;
}
.list p:hover{
    background-image: linear-gradient(315deg, #0d324d 30%, #7f5a83);
    border-radius: 5px;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.deleteIcon{
    margin: 0 5px;
    font-size: 25px;
}
.deleteIcon:hover{
    color: red;
    border: 0;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: white;
    font-size: 18px;
    padding-left: 0px;
    font-family: cursive;
}
.list p input:focus{
    outline: none;
}